<template>
  <div>
    <el-button type="primary" @click="dialogFormAddInit" class="button"></el-button>
    <el-table
        ref="userTable"
        :data="tableData"
        highlight-current-row
        @cell-dblclick="handleCelldbClink"
    >
      <el-table-column
          v-for="(value,name,index) in tableData[0]"
          :label=dealWithChinese(name)
          min-width="100"
          :prop=name
          :key=index>
      </el-table-column>
      <el-table-column
          label=""
          width="180">
        <template slot-scope="scope">
          <el-button
              size="mini"
              type="success"
              @click=dialogFormUpdateInit(scope.row)>
          </el-button>
          <el-popconfirm
              confirmButtonText=''
              cancelButtonText=''
              icon="el-icon-info"
              iconColor="red"
              title=""
              @onConfirm="handleDelete(scope.row.id)"
          >
            <el-button size="mini" type="danger" slot="reference"></el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
        style="text-align: center"
        background
        layout="prev, pager, next"
        @current-change="pageChange"
        :total="total">
    </el-pagination>

    <el-dialog title="" :visible.sync="dialogFormAddVisible">
      <el-form :model="addUserform">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addUserform.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addUserform.accountId" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addUserform.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="addUserform.roleId" clearable placeholder="">
            <el-option
                v-for="item in roleOptions"
                :key="item.id"
                :label="item.roleName"
                :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormAddCancel"> </el-button>
        <el-button type="primary" @click="dialogFormAddConfirm(addUserform)"> </el-button>
      </div>
    </el-dialog>


    <el-dialog title="" :visible.sync="dialogFormUpdateVisible">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.accountId" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.score" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.reputation" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.roleId" clearable placeholder="">
            <el-option
                v-for="item in roleOptions"
                :key="item.id"
                :label="item.roleName"
                :value="item.id">
            </el-option>
          </el-select>
          <el-button type="primary" @click="changeRole(form)" class="button"></el-button>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormUpdateCancel"> </el-button>
        <el-button type="primary" @click="dialogFormUpdateConfirm(form)"> </el-button>
      </div>
    </el-dialog>


  </div>
</template>

<script>
import {
  getUsers,
  getroles,
  adminUpdateUser,
  adminAddUser,
  adminDeleteUser,
  getUserinfoByUserId,
  UserchangeRole
} from "@/api/part3";

export default {
  name: "user",


  created() {
    this.init();
  },
  data() {
    return {
      form: {
        id: "",
        accountId: "",
        name: "",
        email: "",
        score: "",
        reputation: "",
        roleId: ""
      },

      dialogFormUpdateVisible: false,
      dialogFormAddVisible: false,
      formLabelWidth: '120px',
      total: 0,//


      roleOptions: [
        {
          id: 1,
          roleName: ''
        },
        {
          id: 2,
          roleName: ''
        },
      ],
      tableData: [
        // {
        //     "id": 4,
        //     "accountId": "333",
        //     "password": "8c389b98e0e361813d9a10c373c7738a",
        //     "name": "yyy",
        //     "gmtCreate": 1589860197682,
        //     "gmtModified": 1589860197682,
        //     "score": 1,
        //     "reputation": 0,
        //     "email": null,
        //     "role": 1
        // },
      ],
      addUserform: {
        name: "",
        accountId: "",
        password: "",
        roleId: ""
      }
    }
  },
  methods: {
    dealWithChinese(name)
    {
      if(name=="email"){
        name=""
      }
      if(name=="id"){
        name=""
      }
      if(name=="accountId"){
        name=""
      }         if(name=="password"){
      name=""
    }         if(name=="id"){
      name=""
    }         if(name=="score"){
      name=""
    }         if(name=="name"){
      name=""
    }         if(name=="gmtCreate"){
      name=""
    }
      if(name=="gmtModified"){
        name=""
      }
      if(name=="reputation"){
        name=""
      }
      return name
      //


    },
    getUsersAPI() {
      getUsers(1).then(res => {
        console.log("api")
        console.log(res.data)
        this.tableData = res.data.list
        this.total = res.data.total;
      }).catch(err => {
        console.log(err);
        console.log("api")
      });
    },
    getrolesAPI() {
      getroles().then(res => {
        console.log("api");
        console.log(res);
        this.roleOptions = res.data.roleList;
      }).catch(err => {
        console.log(err);
        console.log("api")
      })
    },
    getUserDetailAPI(id) {
      getUserinfoByUserId(id).then(res => {
        console.log("api");
        console.log(res);
        let user = res.data.user
        let role = res.data.role
        this.form.id = user.id
        this.form.accountId = user.accountId
        this.form.name = user.name
        this.form.email = user.email
        this.form.score = user.score
        this.form.reputation = user.reputation
        this.form.roleId = role.id
      }).catch(err => {
        console.log(err);
        console.log("api")
      })
    },
    changeRole(form) {

      console.log(form.id)
      console.log(form.roleId)
      let params = {}
      params.userId = form.id
      params.roleId = form.roleId
      UserchangeRole(params).then(res => {
        console.log("");
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
        this.getUsersAPI();//
      }).catch(err => {
        console.log("");
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      })
    },


    init() {
      this.getUsersAPI();
      this.getrolesAPI();
    },

    handleDelete(id) {
      adminDeleteUser(id).then(res => {
        console.log("api");
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
        this.getUsersAPI();//
      }).catch(err => {
        console.log("api");
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      })
    },


    handleCelldbClink(row) {
      this.dialogFormUpdateInit(row);
    },


    handleCheckChange(data, checked, indeterminate) {
      console.log(this.$refs.tree.getCheckedKeys())
      console.log(data, checked, indeterminate);
    },
    dialogFormUpdateInit(row) {
      this.dialogFormUpdateVisible = true
      console.log(row)
      this.getUserDetailAPI(row.id)
    },
    dialogFormUpdateCancel() {
      this.dialogFormUpdateVisible = false

    },
    dialogFormUpdateConfirm(data) {
      let params = {}
      params.name = data.name
      params.accountId = data.accountId
      params.email = data.email
      params.score = data.score
      params.reputation = data.reputation

      this.dialogFormUpdateVisible = false
      adminUpdateUser(params).then(res => {
        console.log("api");
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
        this.getUsersAPI();
      }).catch(err => {
        console.log("api");
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });

      })
    },

    dialogFormAddInit() {
      this.dialogFormAddVisible = true;
      this.addUserform = {}
    },
    dialogFormAddCancel() {
      this.dialogFormAddVisible = false

    },
    dialogFormAddConfirm(data) {
      this.dialogFormAddVisible = false
      adminAddUser(data).then(res => {
        console.log("api");
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
        this.getUsersAPI();//
      }).catch(err => {
        console.log("api");
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });

      })
    },


    //
    pageChange(page) {
      console.log(page)
    },
    // prevClick(page){
    //     console.log(""+page)
    // },
    // nextClick(page){
    //     console.log(""+page)
    // },
  }
}
</script>

<style scoped lang="stylus">
.button
  margin-bottom 10px
</style>
